import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    music: null,
    logged: false
  },
  mutations: {
    loggedMusic(state) {
      state.logged = true
    },
    logoutMusic(state) {
      state.logged = false
    }
  },
  getters: {
    isLogged: state => {
      return state.logged
    },
  }
});
